/* Css Fonts Imported */
@import url('https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700;800&display=swap');

/* Css Reset  Start */
* {
  margin:0;
  padding: 0;
  font-family: 'Readex Pro', sans-serif;
}

h2{
  font-style: italic;
}


.primary-title {
  font-size: 90px;
  color: rgba(255,255,255);
}

.primary-button {
  color: #fff;
  text-transform: uppercase;
  font-size:15px;
  background: linear-gradient(45deg, rgb(254, 48, 114), rgb(255, 89, 64));
  padding: 12px 30px;
  border-radius: 30px;
  border: none;
}


.primary-button:hover {
  background: linear-gradient(260deg, rgb(254, 48, 114), rgb(255, 89, 64));
  cursor: pointer;
}

.secondary-button {
  color: rgb(121, 119, 119);
  background-color: rgb(255, 255, 255);
  font-weight: 500;
  font-size: 15px;
  text-transform: uppercase;
  padding: 12px 30px;
  border-radius: 30px;
  border: solid 2px rgb(121, 119, 119);
  margin: 6px;
}

.secondary-button:hover {
  color: rgb(48,48,48);
  border: solid 2px rgb(48,48,48);
}


body{
  text-align: center;
}

/* Css Reset  End */


/* Css NavBar Start */
nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

nav .nav-button {
  color: rgb(254, 48, 114);
  background-color: white;
  font-size: 20px;
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  margin: 10px;
}

nav .nav-button:disabled {
  background-color: rgb(181,180,180);
  color: rgb(160,38,75);
}


nav .logo-container {
  width: 170px;
  margin: 15px;
}

nav .logo-container .logo{
  width: 100%;
}

/* Css NavBar End */


/* Css NavBar Start */

/* Css Home Start */
.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.65) 0%,
    rgba(0, 0, 0, 0) 100%
), url('images/6e9794bcedeecf5a8f8f41338a2a7345.webp')
}


.home  {
  margin-top: 30vh;

}
/* Css Home End */



/* Css Auth Modal Start */
.auth-modal {
  position: absolute;
  left: 0;
  right: 0;
  top: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 360px;
  height: 600px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  padding: 40px;
}

.auth-modal .close-icon {
  float: right;
}

.auth-modal form {
  display: flex;
  flex-direction: column;
}

.auth-modal form input[type=password],
.auth-modal form input[type=email]
 {
  padding: 8px;
  margin: 8px;
  font-size: 17px;
}
/* Css Auth Modal End */


/* Css OnBoarding Page Start */

.onboaring form {
  display: flex;
  justify-content:center;
}


.onboaring form section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  text-align: start;
}

.onboaring form input {
  padding: 15px 30px;
  margin: 10px 0;
  font-size: 15px;
  border: solid 2px rgb(219,219,219);
}

.onboaring form .multiple-input-container{
  display: flex;
  flex-direction: row;

}

.onboaring form .multiple-input-container input {
 margin-right: 10px;
}


.onboaring form label {
  margin: 10px 0;
}

.onboaring form .multiple-input-container label {
  padding: 10px;
  border: solid 2px rgb(144,144,144);
  border-radius: 10px;
  transition: all 0.3;
 }

 .onboaring form  input[type='radio'] {  
    display: none;
 }


 .onboaring form  input[type='number'] {  
  width:10% ;
}

 .onboaring form  input[type='radio']:checked + label {  
  border: solid 2px rgb(221,39,22);
}

.onboaring form  input[type='submit']:hover {
  background-color: rgb(235,235,235);
}

.onboaring form  input[type='submit']:active {
  background-color: rgb(226,115,155);
}




/* Css OnBoarding Page End */


/* Css Dashboard Page Start */
h2 {
  color: #fff;
}

.swipe {
  position: absolute;
}

.cardContainer {
  width: 90vw;
  max-width: 260px;
  height: 300px;
}

.card {
  position: relative;
  background-color: #fff;
  width: 80vw;
  max-width: 260px;
  height: 300px;
  box-shadow: 0px 0px 60px 0px rgba(0,0,0,0.30);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.swipe:last-of-type {

}

.card h3 {
  position: absolute;
  bottom: 0;
  margin: 10px;
  color: #fff;
}
/* Css Dashboard Page End */